div.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 90%;
  background-color: #ddd;
  margin: 0 auto;
  color: #222;
}
header {
  flex-basis: 30vh;
  position: relative;
  overflow: hidden;
}
main {
  display: flex;
  flex-grow: 1;

  padding: 30px 0;
}

aside {
  min-width: 240px;
}

section.page {
  flex-grow: 1;
}

footer {
  background: black;
  color: white;
  text-align: center;
  text-transform: uppercase;
  padding: 10px 0;
}

div.footer p {
  font-size: small;
  text-transform: capitalize;
}

div.footer span {
  color: blue;
}

header img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  min-width: 100%;
  min-height: 100%;
}

nav.main {
  /* background: #aaa; */
  padding: 0 30px 30px 0;
}

.main ul {
  list-style: none;
}

nav.main a {
  display: block;
  color: black;
  text-decoration: none;
  font-weight: bold;
  text-transform: uppercase;
  padding: 10px;
  transition: 0.3s;
}

nav.main a:hover {
  transform: translateX(20px);
}

nav.main a.active {
  border-left: 10px solid black;
  transform: translateX(0);
}

nav.main li:nth-child(1) a.active {
  border-left-color: blue;
}

nav.main li:nth-child(2) a.active {
  border-left-color: green;
}

nav.main li:nth-child(3) a.active {
  border-left-color: yellow;
}

nav.main li:nth-child(4) a.active {
  border-left-color: red;
}

div.productPage {
  margin-bottom: 27px;
  text-transform: uppercase;
  text-decoration: underline;
}

article.product {
  margin-bottom: 30px;
}

.returnToProList {
  font-size: small;
}

.products h2 {
  margin-bottom: 20px;
}

.products ul {
  list-style: square;
  padding-left: 30px;
}

.products li {
  margin-bottom: 20px;
}

.products a {
  text-decoration: none;
  color: royalblue;
}

.products a:hover {
  text-decoration: underline;
  color: black;
}

.contact textarea {
  font-size: 16px;
  width: 80%;
  min-height: 200px;
  margin: 20px 0;
  line-height: 1.6;
}

.contact button {
  display: block;
  margin: 0 20% 0 auto;
  border: 2px solid black;
  background-color: white;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 20px;
  transition: 0.3s;
}

.contact button:hover {
  color: yellow;
  font-weight: bold;
  background-color: black;
  border: 3px solid yellow;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: sans-serif;
  font-size: 20px;
}

